export default {
  data () {
    return {
      tabNum:0,
      navList:[
        {
          name:'打卡圈',
          className:'icon-dkq',
          to:'friend',
        },{
          name:'记录',
          className:'icon-jl',
          to:'record',
        },{
          name:'打卡',
          className:'icon-dk',
          to:'card',
        },{
          name:'群组',
          className:'icon-qz',
          to:'group',
        },{
          name:'设置',
          className:'icon-sz',
          to:'set',
        }
      ]
    }
  },
  computed: {
    tabNo(){
      if(localStorage.getItem('tabNum')) {
          let tabNum = localStorage.getItem('tabNum');
          return tabNum
      }else{
        return 0
      }
    }
  },
  methods:{
    tabClick(i){
      this.tabNum=i;
    },
  },
  created(){
    this.tabNum=this.tabNo
  }
}



